"use client";

import HeaderModern from "../components/layout/HeaderModern";
import Footer from "../components/layout/Footer";
import PageHeader from "../components/shared/PageHeader";
import { Car, Construction } from "lucide-react";
import Link from "next/link";

export default function VoituresNeuvesPage() {
    return (
        <div className="min-h-screen bg-[#F8F9FA]">
            <HeaderModern />

            <PageHeader
                icon={Car}
                badge="Catalogue 2025"
                title="Voitures"
                highlight="Neuves"
                description="Découvrez les derniers modèles disponibles chez les concessionnaires officiels en Algérie."
            />

            <section className="py-24 max-w-7xl mx-auto px-6 text-center">
                <div className="bg-white rounded-3xl p-12 shadow-xl border border-gray-100 max-w-2xl mx-auto">
                    <div className="w-20 h-20 bg-[#FF6B35]/10 rounded-full flex items-center justify-center mx-auto mb-6">
                        <Construction className="w-10 h-10 text-[#FF6B35]" />
                    </div>
                    <h2 className="text-3xl font-bold text-[#1A1A2E] mb-4">Bientôt Disponible</h2>
                    <p className="text-gray-600 mb-8 text-lg">
                        Nous travaillons activement sur le catalogue des voitures neuves.
                        En attendant, découvrez nos occasions récentes.
                    </p>
                    <Link
                        href="/occasions"
                        className="inline-flex items-center justify-center px-8 py-4 bg-[#FF6B35] text-white font-bold rounded-xl hover:bg-[#E85D2E] transition-all shadow-lg shadow-[#FF6B35]/30"
                    >
                        Voir les occasions
                    </Link>
                </div>
            </section>

            <Footer />
        </div>
    );
}
